export default ".login-section {\n  width: 100%;\n  height: 100%;\n  display: flex;\n  align-items: center;\n}\n\n.login-section-inner {\n  width: 595px;\n  min-height: 400px;\n  margin: 0 auto;\n  box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.175);\n  border-radius: 8px;\n  position: relative;\n  /* padding: 25px 40px; */\n  display: flex;\n}\n\n.login-section-inner .login-left {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  flex: 1;\n  background: url('bg-left.png');\n  flex-direction: column;\n  background-size: cover;\n  background-repeat: no-repeat;\n}\n\n.login-section-inner .login-left img {\n  width: 50%;\n}\n\n.login-right {\n  flex: 1;\n  padding: 0 0 20px 0;\n  padding: 25px 40px;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n}\n\n.headerText {\n  width: 100%;\n  color: #BC2E65;\n  font-size: 24px;\n  font-weight: 700;\n  height: 1em;\n  margin-top: 10px;\n  margin-bottom: 10px;\n}\n\n.login-section-inner label {\n  font-size: 15px;\n  color: #4D4D55;\n  font-family: \"Roboto\";\n}\n\n.login-section-inner input {\n  font-size: 13px;\n  border: 1px solid #D2D4DF;\n  border-radius: 8px;\n  color: #9595A1;\n  height: 35px;\n  font-family: \"Roboto\";\n}\n\n.login-section-inner .btn-sign-in {\n  width: 100%;\n  border-color: transparent;\n  transition: 0.5s;\n  background-size: 200% auto;\n  font-size: 13px;\n  border-radius: 8px;\n  height: 35px;\n  font-family: \"Roboto\";\n  margin-top: 10px;\n  background: transparent linear-gradient(90deg, #783A87 0%, #BC2E65 100%) 0% 0% no-repeat padding-box;\n}\n\n.login-section-inner .btn-sign-in:hover {\n  /* background-position: right center; */\n  background: transparent linear-gradient(90deg, #783A87 0%, #BC2E65 100%) 0% 0% no-repeat padding-box;\n}\n\n.login-section-inner .form-control:focus {\n  border-color: #9C3EE8;\n  outline: 0;\n  box-shadow: 0 0 0 0 #9C3EE8;\n}\n\n.login-section-inner .form-group {\n  margin-bottom: 10px;\n}\n\n.login-section-inner .btn-primary:not(:disabled):not(.disabled):active {\n  background: transparent linear-gradient(90deg, #783A87 0%, #BC2E65 100%) 0% 0% no-repeat padding-box;\n}\n\n.sign-up {\n  font-size: 12px;\n  color: #4D4D55;\n  cursor: pointer;\n  font-family: \"Roboto\";\n  text-align: center;\n  padding-top: 10px;\n}\n\n.sign-up a {\n  color: #c92c5f;\n  text-decoration: none;\n  padding-left: 10px;\n}\n\n@media (max-width: 991px) {\n  .login-section-inner {\n    display: block;\n  }\n\n  .login-section-inner .login-left {\n    min-height: 225px;\n  }\n\n  .login-section-inner .login-left img {\n    width: 35%;\n  }\n}\n\n@media (max-width: 767px) {\n  .login-section {\n    align-items: inherit;\n  }\n}\n/*# sourceMappingURL=data:application/json;base64, */"